.contenedor-testimonio{
  width: 1170px;
  height: 368px;
  margin: 10px 10px;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  box-shadow: 0 3px 13px 1px rgb(0 0 0 / 9%);

}
.imagen-testimonio {
  height: 100%;
  width: auto;
}

.contenedor-texto-testimonio{

  text-align: center;
  padding: 40px;
  font-family: Lato, sans-serif;
  font-weight: 400;
  line-height: 1.5rem;

}
.nombre-testimonio{
  font-size: 1.5em;
}
  
.cargo-testimonio{
  font-size: 1.5rem;
  margin-top: 15px;
  padding-bottom: 30px;
}
.text-testimonio{
  font-size: 1.3rem;
  text-align: justify;

}